<template>
  <div class="register">
    <xheader :title="''" :backIcon='true' @back='back'></xheader>
    <div class="register-content">
      <Form ref="formCustom" :model="formCustom" :rules="ruleCustom" :label-width="80">
        <Form-item label="" prop="user">
          <Input type="text" v-model="formCustom.user" placeholder="Username"></Input>
        </Form-item>
        <Form-item label="" prop="passwd">
          <Input type="password" v-model="formCustom.passwd"></Input>
        </Form-item>
        <Form-item label="" prop="passwdCheck">
          <Input type="password" v-model="formCustom.passwdCheck"></Input>
        </Form-item>
        <Form-item>
          <Button type="primary" @click="handleSubmit('formCustom')"></Button>
          <Button type="ghost" @click="handleReset('formCustom')" style="margin-left: 8px"></Button>
        </Form-item>
      </Form>
    </div>
  </div>
</template>

<script>
  import xheader from './public/header'
  export default{
    data () {
      const validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''))
        } else if (value.length < 6) {
          callback(new Error('6'))
        } else {
          if (this.formCustom.passwdCheck !== '') {
            this.$refs.formCustom.validateField('passwdCheck')
          }
          callback()
        }
      }
      const validatePassCheck = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''))
        } else if (value !== this.formCustom.passwd) {
          callback(new Error('!'))
        } else {
          callback()
        }
      }
      return {
        formCustom: {
          passwd: '',
          passwdCheck: '',
          age: ''
        },
        ruleCustom: {
          user: [
          { required: true, message: '', trigger: 'blur' }
          ],
          passwd: [
          { validator: validatePass, trigger: 'blur' }
          ],
          passwdCheck: [
          { validator: validatePassCheck, trigger: 'blur' }
          ]
        }
      }
    },
    components: {
      xheader
    },
    methods: {
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.register()
          } else {
            this.$Message.error('!')
          }
        })
      },
      handleReset (name) {
        this.$refs[name].resetFields()
      },
      back () {
        this.$router.go(-1)
      },
      register () {
        this.$http.post('/identify/register', {username: this.formCustom.user, password: this.formCustom.passwd}).then((res) => {
          console.log(res)
          if (res.data === 'fail') {
            this.$Message.success('')
            this.handleReset('formCustom')
          }
          if (res.data === 'success') {
            this.$Message.success('')
            this.$router.push('/mine')
          }
        })
      }
    }
  }
</script>

<style lang='scss'>
  .register{
    .register-content{
      padding:30px;
      margin-top:50px;
    }
  }
</style>



